body {
    font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3 {
    color: #000;
}

h1 {
    font-size: 32px;
}

h2 {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 20px;
    margin-top: 15px;
}

h3 {
    font-size: 18px;
    font-weight: 600;
}

.bg-formend {
    background: url("../images/bg-formend.png") no-repeat center center fixed #8e582d; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;      
}

.bg-formstart {
    background: url("../images/bg-formstart.png") no-repeat center center fixed #835f5a; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;    
}

.message {
    width: 610px;
    background-color: #fff;
    margin: 10% auto 0 auto;
    border-radius: 3px;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.6);
    border: 1px solid #c7c7c7;
    
    .message-header {
        margin: -1px -1px 0 -1px;
        height: 190px;
        background-color: #313236;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
        
        .logo {
            display: block;
            text-align: center;
            padding-top: 30px;            
            
            img {
                width: 138px;
                height: 127px;                
            }
        }
    }
    
    .message-body {
        padding: 30px;
        text-align: center;
        font-weight: 600;        
        
        .big-txt {
            font-size: 20px;
            margin-bottom: 20px;
        }
    }
}

//************ Form
label {
    font-weight: normal;
    color: #555;
}

.form-control,
.radio-inline,
.checkbox label {
    color: #000;
}

.form-simple {
    width: 700px;
    height: 450px;
    position: relative;
    background-color: #fff;
    margin: 5% auto 0 auto;
    border-radius: 3px;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.6);
    
    .form-simple-body {
        width: 400px;
        padding: 60px;
        margin-right: 300px;
    }
    
    .form-simple-title {
        margin-bottom: 30px;
        text-align: center;

        h1 {
            font-size: 26px;
            font-weight: 600;
            margin: 0 0 10px 0;
        }
    }
    
    .form-control, 
    .btn-primary {
        height: 42px;
        padding: 8px 16px;
        font-size: 16px;
        border-radius: 3px;
    }

    .btn-primary {
        width: 150px;
    }    
    
    .form-group {
        margin-bottom: 20px;
    }
    
    .form-group-captcha {
        .form-control {
            float: right;
            width: 110px;
            margin-top: 4px;
        }
        
        .img-captcha {
            float: left;
            width: 120px;
        }
        
        .btn-reload {
        	float: left;
        	background-color: transparent;
        	font-size: 16px;
        	margin-top: 6px;
        }
    }
    
    .form-btn {
        text-align: center;
    }
    
    .form-simple-info {
        width: 300px;
        padding: 45px 60px;
        height: 100%;
        background-color: #313236;
        color: #fff;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
        position: absolute;
        top: 0;
        right: 0;
        
        .logo {
            display: block;
            margin-bottom: 60px;
            text-align: center;

            img {
                width: 138px;
                height: 127px;
            }
        }
        
        h2 {
            font-weight: 600;
            font-size: 20px;
            color: #fff;
            margin: 25px 0;
        }
    }
    
    .list-contacts {
        margin: 0;
        padding: 0;
        list-style: none;
        
        li {
            margin-bottom: 15px;
            
            a {
                color: #468cc8;
                
                &:hover,
                &:focus {
                    color: darken(#468cc8, 15%);
                }
                
                &:before {
                    display: inline-block;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;					
					font-family: 'Font Awesome 5 Brands';
                    font-size: 18px;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
					line-height: 1;  
                    color: #fff;
                    margin-right: 10px;
                }
                
                &.vk:before {
                    content: "\f189";
                }
                &.instagram:before {
                    content: "\f16d";
                }
                &.site {
                	
                	&:before {
                		font-family: 'Font Awesome 5 Pro';
                		content: "\f7a2";
                	}
                }
            }
        }
    }
}